@import './../../Styles/variables.scss';

.header{
    position: fixed;
    top: 0;
    left: 0;
    background: $light;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.4);
    width: 100%;
    height: $headerHeightMobile;
    padding: 0 1.5rem;
    z-index: 999;
    color: $dark;
    transition: 0.3s ease all;
    &.scroller{
        transition: 0.75s ease all;
        background: none;
        box-shadow: none;
        color: $light;
    }
    @include breakpoint(md) {
        height: $headerHeightDesktop;
        padding: 0 3rem;
    }
    

    &__content{
        overflow: visible;
        margin: 0 auto;
        max-width: 2200px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        z-index: 100;
        @include breakpoint(md) {
            height: 100%;
        }

        &__logo{
            font-size: 1rem;
            font-weight: bold;
            text-decoration: none;
            @include breakpoint(md) {
                font-size: 2rem;
            }
        }

        &__nav{
            top: 0;
            right: 100%;
            bottom: 0;
            width: 100%;
            height: 100vh;
            position: fixed;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            background: rgba($dark, 0.9);
            backdrop-filter: blur(2px);
            transform: translate(0);
            transition: 0.3s ease transform;
            
            @include breakpoint(md) {
                transform: none;
                flex-direction: row;
                background: transparent;
                width: auto;
                height: 100%;
                position: static;
            }
            ul {
                list-style: none;
                padding: 0;
                display: flex;
                flex-direction: column;
                margin-bottom: $spacing-lg;
                @include breakpoint(md) {
                    flex-direction: row;
                    align-items: center;
                    margin-bottom: 0;
                    margin-right: calc(0.5rem + #{$spacing-md});
                }
                li {
                    &:not(:last-child) {
                        margin-bottom: $spacing-lg;
                        @include breakpoint(md) {
                            margin-bottom: 0;
                            margin-right: $spacing-md;
                        }
                    }
                    a{
                        font-size: 1.2rem;
                        text-decoration: none;
                        color: inherit;
                        padding: 1rem 2rem;
                        border-radius: $borderRadius;
                        transition: 0.3s ease all;

                        &:hover {
                            background: rgba($light, 0.1);
                        }
                        &:active {
                            border-radius: calc(#{$borderRadius} + 6px);
                            background: linear-gradient(rgba($light, 0.1), rgba($light, 0.2));
                        }
                    }
                }
            }
            button{
                cursor: pointer;
                outline: none;
                padding: 0.75rem 1.7rem;
                border-radius: $borderRadius;
                font-size: 1.2rem;
                font-family: inherit;
                background: $primary;
                color: $dark;
                border: 1px solid transparent;
                transition: 0.3s ease all;
                &:hover{
                    background-color: $primary;
                    background: rgba($primary, 0.1);
                    color: $primary;
                }
                //active is when it is pressed moment
                &:active{
                    background-color: $primary;
                    background: linear-gradient(rgba($primary, 0.2), rgba($primary, 0.3));
                    color: $primary;
                }
            }

            &.isMenu{
                transform: translate(100%, 0%);
                color: $light;
            }
        }

        &__toggle{
            cursor: pointer;
            display: flex;
            align-items: center;
            font-size: 2rem;
            transition: 0.3s ease all;
            position: relative;

            &:hover{
                color: $primary;
            }
            @include breakpoint(md){
                display: none;
            }
        }

    }

}